import React, { useEffect } from "react";

import Navbar from '../../genral_components/Navbar/Navbar'

import "./BlogNewPage.css"

export default function BlogNewPage() {

    let titleOfThePage

    useEffect(() => {
        // Retrieve the hash value and parse the data to set the props accordingly
        const hashData = window.location.hash.substr(1);

        console.log("Hash data" , hashData)

        const data = JSON.parse(decodeURIComponent(hashData));

        console.log("Data" , data)

        titleOfThePage = data.title;

        console.log(titleOfThePage)
        // Do something with the props...
      }, []);

  return (
    <div className='blog-new-page-main-container'>
        <Navbar/>

        <div className='blog-new-page-main-container-secondary'>

            <div className='blog-new-page-main-container-secondary-main-image'>
                {titleOfThePage}
            </div>
        </div>
    </div>
  )
}
